import useInput from '@hooks/useInput';
import {
  BlankDiv,
  Button,
  Error,
  Form,
  Header,
  IconButton,
  Input,
  InputContainer,
  Label,
  LinkContainer,
  Success,
} from '@pages/member/SignUp/styles';
import React, { useCallback, useEffect, useState } from 'react';
import { Link, useLocation, useNavigate } from 'react-router-dom';
import useCustomMember from '@hooks/useCustomMember';
import { toast } from 'react-toastify';
import { AiOutlineEye, AiOutlineEyeInvisible } from 'react-icons/ai';

const SignUp = () => {
  const navigate = useNavigate();

  const { doLogin, memberState, doSignup } = useCustomMember();
  const location = useLocation();
  const { from } = location.state || { from: { pathname: '/workspace/mqnavigator' } };
  const { updateUrlAfterLogin } = useCustomMember();

  const [email, onChangeEmail] = useInput('');
  const [nickname, onChangeNickname] = useInput('');
  const [password, , setPassword] = useInput('');
  const [passwordCheck, , setPasswordCheck] = useInput('');

  const [signUpError, setSignUpError] = useState(false);
  const [signUpSuccess, setSignUpSuccess] = useState(false);
  const [mismatchError, setMismatchError] = useState(false);
  const [showPassword, setShowPassword] = useState(false);
  const [showPasswordCheck, setShowPasswordCheck] = useState(false);

  const togglePasswordVisibility = () => {
    setShowPassword((prev) => !prev);
  };

  const togglePasswordCheckVisibility = () => {
    setShowPasswordCheck((prev) => !prev);
  };

  const onChangePassword = useCallback(
    (e: React.ChangeEvent<HTMLInputElement>) => {
      setPassword(e.target.value);
      setMismatchError(passwordCheck !== e.target.value);
    },
    [passwordCheck, setPassword],
  );

  const onChangePasswordCheck = useCallback(
    (e: React.ChangeEvent<HTMLInputElement>) => {
      setPasswordCheck(e.target.value);
      setMismatchError(password !== e.target.value);
    },
    [password, setPasswordCheck],
  );

  const onSubmit = useCallback(
    (e: React.FormEvent<HTMLFormElement>) => {
      e.preventDefault();
      if (!nickname || !nickname.trim()) {
        return;
      }
      if (!mismatchError) {
        setSignUpError(false);
        setSignUpSuccess(false);
        const singupParam = { email: email, nickname: nickname, password: password };
        doSignup(singupParam)
          .then(() => {
            toast.dismiss();
            toast.success(`Signup was successful`, {
              closeOnClick: true,
            });
            updateUrlAfterLogin().then();
          })
          .catch((error) => {
            // console.log(error.response?.data);
            toast.error('Failed to signup', { position: 'bottom-center' });
            setSignUpError(error.response?.data?.code === 403);
          });
      }
    },
    [doLogin, email, mismatchError, nickname, password, updateUrlAfterLogin],
  );

  useEffect(() => {
    if (memberState.email) {
      navigate(from.pathname, { replace: true });
    }
  }, [memberState.email, from.pathname, navigate]);

  return (
    <>
      <Header>MQ-Navigator</Header>
      <Form onSubmit={onSubmit}>
        <Label id="email-label">
          <span>Email Address</span>
          <InputContainer>
            <Input type="email" id="email" name="email" value={email} onChange={onChangeEmail} />
            <BlankDiv></BlankDiv>
          </InputContainer>
        </Label>
        <Label id="nickname-label">
          <span>Nickname</span>
          <InputContainer>
            <Input type="text" id="nickname" name="nickname" value={nickname} onChange={onChangeNickname} />
            <BlankDiv></BlankDiv>
          </InputContainer>
        </Label>
        <Label id="password-label">
          <span>Password</span>
          <InputContainer>
            <Input
              type={showPassword ? 'text' : 'password'}
              id="password"
              name="password"
              value={password}
              onChange={onChangePassword}
            />
            <IconButton type="button" onClick={togglePasswordVisibility}>
              {showPassword ? <AiOutlineEyeInvisible size={20} /> : <AiOutlineEye size={20} />}
            </IconButton>
          </InputContainer>
        </Label>
        <Label id="password-check-label">
          <span>Confirm Password</span>
          <InputContainer>
            <Input
              type={showPasswordCheck ? 'text' : 'password'}
              id="password-check"
              name="password-check"
              value={passwordCheck}
              onChange={onChangePasswordCheck}
            />
            <IconButton type="button" onClick={togglePasswordCheckVisibility}>
              {showPasswordCheck ? <AiOutlineEyeInvisible size={20} /> : <AiOutlineEye size={20} />}
            </IconButton>
          </InputContainer>
          {mismatchError && <Error>Passwords do not match.</Error>}
          {!nickname && <Error>Please enter a nickname.</Error>}
          {signUpError && <Error>The email is already registered.</Error>}
          {signUpSuccess && <Success>Sign-up completed! Please log in.</Success>}
        </Label>
        <Button type="submit">Sign Up</Button>
      </Form>
      <LinkContainer>
        Already a member?&nbsp;
        <Link to="/member/login">Go to Login</Link>
      </LinkContainer>
    </>
  );
};

export default SignUp;
